import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import {
  Container, Divider, Form, Grid, Header, Label, Loader, TextArea
} from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const isAnagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  return word1Sorted === word2Sorted;
}

const isMetagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word1.length; i++) {
    if (word1.charAt(i) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetanagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 1;
}

const isPlusogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  let hasDifference = false;
  for (let i = 0; i < word2Sorted.length; i++) {
    if (word1Sorted.charAt(i - +hasDifference) !== word2Sorted.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isLogogrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +hasDifference) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isLogogrif2 = (word1, word2) => {
  if (word2.length - word1.length !== 2) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - totalDifferences) !== word2.charAt(i)) {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isMetagrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) !== word2.charAt(i)) {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isTlenogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 2;
}

const isBrukva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isTikva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1 || totalDifferences === 2) {
        return false;
      }
    } else {
      if (totalDifferences === 3) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 3;
}

const createHybrid = (word1, word2) => {
  const intersection = word1.slice(-3);
  if (word2.slice(0, 3) === intersection) {
    return word1.slice(0, -3) + intersection.toUpperCase() + word2.slice(3);
  }

  return null;
}

const createSharada = (word1, word2, dictionary) => {
  const word = word1 + word2;
  if (dictionary.has(word)) {
    return word;
  }

  return null;
}

const createGolovahvosts = (word1, word2, dictionary) => {
  if (word1.length < 2 || word2.length < 2) {
    return [];
  }

  const golovahvosts = [];
  for (let i = 1; i < word1.length; i++) {
    const word1Tail = word1.slice(i);

    for (let j = 1; j < word2.length; j++) {
      const word = word1Tail + word2.slice(0, -j);
      if (word.length > 3 && dictionary.has(word)) {
        golovahvosts.push(word1.slice(0, i) + word.toUpperCase() + word2.slice(-j));
      }
    }
  }

  return golovahvosts;
};

const App = () => {
  const [dictionary, setDictionary] = useState(null);
  const [data, handleDataChange] = useField('');
  const [dataDebounced] = useDebounce(data, 500);

  useEffect(() => {
    (async () => {
      const { sanstv, sociation } = await import('./dictionary.json');

      setDictionary({
        sanstv: new Set(sanstv),
        sociation: new Set(sociation),
      });
    })();
  }, []);

  if (!dictionary) {
    return <Loader active>Загружаю базу...</Loader>;
  }

  const words = dataDebounced
    .trim()
    .toLowerCase()
    .replace('ё', 'е')
    .split(/\s+/)
    .filter((word, index, self) => {
      return index === self.indexOf(word);
    });

  const anagrams = [];
  const plusograms = [];
  const metagrams = [];
  const metanagrams = [];
  const tlenograms = [];
  const logogrifs = [];
  const logogrifs2 = [];
  const metagrifs = [];
  const brukvas = [];
  const tikvas = [];
  const hybrids = [];
  const sharadas = [];
  const golovahvosts = [];

  words.forEach((word1, i) => {
    words.forEach((word2, j) => {
      if (j === i) {
        return;
      }

      if (j > i) {
        if (isAnagram(word1, word2)) {
          anagrams.push(`${word1} ${word2}`);
        }

        if (isMetagram(word1, word2)) {
          metagrams.push(`${word1} ${word2}`);
        }

        if (isMetanagram(word1, word2)) {
          metanagrams.push(`${word1} ${word2}`);
        }
      }

      if (isTlenogram(word1, word2)) {
        tlenograms.push(`${word1} ${word2}`);
      }

      if (isPlusogram(word1, word2)) {
        plusograms.push(`${word1} ${word2}`);
      }

      if (isLogogrif(word1, word2)) {
        logogrifs.push(`${word1} ${word2}`);
      }

      if (isLogogrif2(word1, word2)) {
        logogrifs2.push(`${word1} ${word2}`);
      }

      if (isMetagrif(word1, word2)) {
        metagrifs.push(`${word1} ${word2}`);
      }

      if (isBrukva(word1, word2)) {
        brukvas.push(`${word1} ${word2}`);
      }

      if (isTikva(word1, word2)) {
        tikvas.push(`${word1} ${word2}`);
      }

      const hybrid = createHybrid(word1, word2);
      if (hybrid) {
        hybrids.push(hybrid);
      }

      const sharada = createSharada(word1, word2, dictionary.sanstv);
      if (sharada) {
        sharadas.push(sharada);
      }

      golovahvosts.push(...createGolovahvosts(word1, word2, dictionary.sociation));
    });
  });

  return (
    <Container>
      <Grid padded>
        <Grid.Row>
          <Grid.Column>
            <Form>
              <TextArea
                autoFocus
                value={data}
                placeholder="Данные"
                onChange={handleDataChange}
              />
            </Form>
          </Grid.Column>
        </Grid.Row>
        {anagrams.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Анаграммы</Header>
              </Divider>
              <div className={styles.words}>
                {anagrams.map((anagram, i) => (
                  <Label
                    horizontal
                    key={anagram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {anagram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {plusograms.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Плюсограммы</Header>
              </Divider>
              <div className={styles.words}>
                {plusograms.map((plusogram, i) => (
                  <Label
                    horizontal
                    key={plusogram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {plusogram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {metagrams.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Метаграммы</Header>
              </Divider>
              <div className={styles.words}>
                {metagrams.map((metagram, i) => (
                  <Label
                    horizontal
                    key={metagram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {metagram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {metanagrams.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Метанаграммы</Header>
              </Divider>
              <div className={styles.words}>
                {metanagrams.map((metanagram, i) => (
                  <Label
                    horizontal
                    key={metanagram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {metanagram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {tlenograms.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Тленограммы</Header>
              </Divider>
              <div className={styles.words}>
                {tlenograms.map((tlenogram, i) => (
                  <Label
                    horizontal
                    key={tlenogram}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {tlenogram}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {logogrifs.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Логогрифы</Header>
              </Divider>
              <div className={styles.words}>
                {logogrifs.map((logogrif, i) => (
                  <Label
                    horizontal
                    key={logogrif}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {logogrif}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {logogrifs2.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Логогрифы +2</Header>
              </Divider>
              <div className={styles.words}>
                {logogrifs2.map((logogrif2, i) => (
                  <Label
                    horizontal
                    key={logogrif2}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {logogrif2}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {metagrifs.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Метагрифы</Header>
              </Divider>
              <div className={styles.words}>
                {metagrifs.map((metagrif, i) => (
                  <Label
                    horizontal
                    key={metagrif}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {metagrif}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {brukvas.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Брюквы</Header>
              </Divider>
              <div className={styles.words}>
                {brukvas.map((brukva, i) => (
                  <Label
                    horizontal
                    key={brukva}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {brukva}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {tikvas.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Тыквы</Header>
              </Divider>
              <div className={styles.words}>
                {tikvas.map((tikva, i) => (
                  <Label
                    horizontal
                    key={tikva}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {tikva}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {hybrids.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Гибриды</Header>
              </Divider>
              <div className={styles.words}>
                {hybrids.map((hybrid, i) => (
                  <Label
                    horizontal
                    key={hybrid}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {hybrid}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {sharadas.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Шарады</Header>
              </Divider>
              <div className={styles.words}>
                {sharadas.map((sharada, i) => (
                  <Label
                    horizontal
                    key={sharada}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {sharada}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
        {golovahvosts.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <Divider horizontal>
                <Header as="h4" className={styles.title}>Головахвосты</Header>
              </Divider>
              <div className={styles.words}>
                {golovahvosts.map((golovahvost, i) => (
                  <Label
                    horizontal
                    key={golovahvost}
                    className={classNames(styles.word, i % 2 && styles.odd)}
                  >
                    {golovahvost}
                  </Label>
                ))}
              </div>
            </Grid.Column>
          </Grid.Row>
        )}
      </Grid>
    </Container>
  );
};

export default withAuth(App);
